<template>
    <div class="box" id="box">
        
    </div>
</template>

<script>
export default {
    mounted(){
        var map = new BMap.Map("box");          //   
        var point = new BMap.Point(120.5318339559, 27.5886985629);  //   
        map.centerAndZoom(point, 17);    
        var marker = new BMap.Marker(point);  // 
	    map.addOverlay(marker);               // 
        marker.setAnimation(BMAP_ANIMATION_BOUNCE); //
        var label = new BMap.Label("",{offset:new BMap.Size(20,-10)});
        marker.setLabel(label);
        var opts = {
        width : 100,     // 
        height: 50,     // 
        title : "" , // 
        enableMessage:true,//
        message:"~"
        }
        var infoWindow = new BMap.InfoWindow("", opts);  //  
        marker.addEventListener("click", function(){          
            map.openInfoWindow(infoWindow,point); //
        });
        map.addControl(new BMap.MapTypeControl({
		mapTypes:[
            BMAP_NORMAL_MAP,
            BMAP_HYBRID_MAP
        ]}));	  
	    map.enableScrollWheelZoom(true);     
    }
}
</script>
<style scoped>
    html{height:100%}  
    body{height:100%;margin:0px;padding:0px} 
    .box{width:350px;height:200px;position: absolute;left:-100px;z-index: 1000;}
</style>
